
/* css variables for theming */
:root {
  --main-bg-color: #ebeff2;
  --main-font-color: rgb(0, 0, 0);
  --theme-primary: #194761;
  --color-accent: rgb(250, 172, 4);
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  color: #000;
}
.body_msg{
  margin-top: 70px;
  margin-left: 100px;
}
.mini{
  margin-top:-100px;
  /* background-color: red; */
}
.action-btn{
  margin-right: 15px;
  border-radius: 5px;
  background-color: #ebeff2;
  color: #194761;
  font-weight: 600;
  
}
.action-btn:hover{
  background-color: #194761;
  color: #fff;
}


.banner{
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end; 
}
.link{
  padding: 5px;
  background-color: azure;
  margin: 5px;
  text-decoration: none;

  /* display:block; */
}


.live_events{

  text-decoration: underline var(--color-accent);

  /* padding: 10px;
  background-color: rgb(255, 221, 0);
  border-radius:100%; */

}

body {
  font-family: "Prompt", sans-serif;
  background-color: var(--main-bg-color);
}

h1,
h2,
h3,
h4 {
  /* font-family: "Sriracha", cursive; */
  margin-bottom: 0.75em;
}

.card {
  margin: 2rem 0;
  padding: 1rem;
  background-color: white;
  /* border-radius: 1rem; */
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}
.btn{
  margin-top: 20px;
  padding: 10px;
  background-color:var(--theme-primary);
  border: none;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  margin-right: 5px;
  color: #fff;
  border-radius: 5px;
}

.error-text{
  color: red;
}
.error-msg{
  border: 1px solid red;
  background-color: antiquewhite;
  text-align: center;
  margin-bottom: 10px;
  margin-top: 10px;
  height: 40px; 
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5px;
  flex-direction: column;


}

header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0.75rem;
  background-color: var(--theme-primary);
  color: #fff;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}
.heading{
  display:block;
  margin-bottom: 20px;
}
.sub_heading{
  display:block;
}

header h1,
header h3 {
  margin: 0;
}

.filters {
  display: flex;
  align-items: center;
}

.filters input[type="text"] {
  font-size: 14px;
  padding: 0.25rem;
  margin: 0 1rem 0 0.25rem;
}

main {
  width: 80vw;
  max-width: 800px;
  margin: 0 auto;
}

.spice-item {
  display: flex;
  align-items: flex-start;
}

.spice-item img {
  max-width: 400px;
  object-fit: contain;
}

.spice-item .details {
  margin-left: 1rem;
  position: relative;
}

.spice-item .details h2 {
  margin-top: 0;
}

.spice-item .favorite {
  position: absolute;
  font-size: 2rem;
  bottom: 0;
  right: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.spice-item .favorite:focus {
  outline: none;
}

.spice-item .favorite span {
  color: transparent;
  text-shadow: 0 0 0 var(--theme-primary);
}

.spice-item .details p {
  margin: 0.75rem 0;
}

.star-rating {
  color: rgba(0, 0, 0, 0.1);
  font-size: 1.5rem;
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.star-rating .foreground {
  color:var(--color-accent);
  position: absolute;
  display: flex;
  overflow: hidden;
}

.star-rating .background {
  display: flex;
}
.form_container{
  display: flex;
  justify-content: center;
  width: 100%;
  /* border: 1px solid red; */
  margin-top: 100px;
}
.new_event{
  width: 100%;
}

form {
  display: flex;
  flex-direction: column;
  width: 50%;

}

form input,
form textarea {
  margin-bottom: 0.7rem;
  border: 1px solid grey;
  padding: 5px;
  border-radius: 5px;
 
}

form button[type="submit"] {
  width: 60px;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid grey;
}
.footer{
  position: absolute;
  text-align: center;
  bottom: -100px;
  width: 100%;
  height: 1.0rem; 
}
.footer-container{
  width: 100vw;
}
.footer-container > ul{
  list-style: none;
}
.row-container{
  display: flex;
  flex-direction: row;
  background-color:var(--color-secondary);
  justify-content: space-evenly;
}
.copyright{
  color: black;
  padding-bottom: 10px;
  font-weight: 600;
  margin-top: 2px;
  margin-bottom: 2px;
}
a{
  text-decoration: none;
}
@media screen and (max-width:600px) {

  .heading{
    display:block;
    margin-bottom: 5px;
  }

  
}
